import MxDatePicker from "@/components/mx-datepicker/mx-datepicker.vue";
import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	components: {
		MxDatePicker
	},
	data() {
		return {
			datalist: [],
			loadingText: '正在加载...',
			hasmore: true,
			curpage: 1,
			tg_predepoit: '',
			showtype: 0,
			placeholder: '输入商家/会员名称',
			keyword: '',
			showPicker: false,
			range: [new Date().toISOString().slice(0, 10), new Date().toISOString().slice(0, 10)],
			type: 'rangedate',
			value: ''
		}
	},
	computed: mapState(['hasLogin', 'userInfo']),
	onLoad() {
		console.log('页面加载');
		this.$Request.post('member_index/my_asset', {
			fields: 'tg_predepoit'
		}).then(res => {
			if (res.code == 200) {
				console.log(res);
				this.tg_predepoit = res.datas.tg_predepoit;
			}
		});
		this.loadData();
	},
	onReachBottom() {
		console.log('正在加载中...');
		this.curpage++;
		this.loadData();
	},
	onPageScroll() {
		console.log('页面滚动...');
	},
	onPullDownRefresh() {
		console.log('上拉刷新...');
		uni.stopPullDownRefresh();
	},
	methods: {
		async loadData() {
			var _this = this;
			console.log('===userInfo===' + this.hasLogin);
			console.log(this.userInfo);
			console.log(this.userInfo.token);
			if (!this.hasLogin) {
				uni.navigateTo({
					url: 'login',
				})
			} else {
				if (!this.hasmore) {
					this.loadingText = '暂无更多信息';
					//return false;
				}
				let postData = {
					curpage: _this.curpage,
					type: _this.showtype
				};
				//if (_this.keyword != '') {
					postData = {
						curpage: _this.curpage,
						type: _this.showtype,
						keyword: _this.keyword,
						daterange: _this.value
					};
				//}
				_this.$Request.post('member_fund/tglist', postData).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.predepoit = res.datas.amount;
						_this.hasmore = res.hasmore;
						console.log(_this.hasmore);
						let _datalist = res.datas.list;
						_this.datalist = [];
						for (var i = 0; i < _datalist.length; i++) {
							_this.datalist.push(_datalist[i]);
						}
						if (!res.hasmore) {
							_this.loadingText = '暂无更多信息';
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: res.datas.error,
							duration: 2000
						});
						uni.navigateTo({
							url: 'login',
						});
					}
				});
			}
		},
		onShowDatePicker(type) { //显示
			this.type = type;
			console.log(type);
			this.showPicker = true;
			this.value = this[type];
		},
		onSelected(e) { //选择
			var _this = this;
			this.showPicker = false;
			if (e) {
				this[this.type] = e.value;
				this.value = e.value;
				//选择的值
				console.log('value => ' + e.value);
				//原始的Date对象
				console.log('date => ' + e.date);
			}
			//
			_this.loadData();
		},
		toPage: function(url) {
			uni.navigateTo({
				url: url,
			})
		},
		showtab(t) {
			this.showtype = t;
			if (t == 0) {
				this.placeholder = '输入商家/会员名称';
			} else if (t == 1) {
				this.placeholder = '输入商家名称';
			} else if (t == 2) {
				this.placeholder = '输入会员名称';
			}
			this.hasmore = true;
			this.loadingText = '正在加载...';
			this.datalist = [];
			this.keyword = '';
			this.loadData();
		},
		submitSearch: function() {
			console.log(this.keyword);
			this.hasmore = true;
			this.loadingText = '正在加载...';
			this.datalist = [];
			this.loadData();
		},

	}
}
